
*{
	padding: 0;
	margin: 0;
}
.edit{
    display:block ;
    width:80%;
    height: 35px;
    line-height: 35px;
    margin: 30px auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-left: 4px;
    border-radius: 4px;
    border:1px solid #ccc;
    outline: 0;
    -webkit-box-shadow: 0 0 5px #ccc;
            box-shadow: 0 0 5px #ccc;
 	text-indent: 10px;
}
.task{
 	display:block ;
    width:80%;
     margin: 30px auto;
}
ul,li{
 	list-style: none;
}
ul li{
 	padding: 10px 0;
}
.delete{
 	display: inline-block;
 	background-color: #f00000;
 	color: #fff;
 	padding: 2px;
 	border:none;
 	margin-left: 10px;
 	border-radius: 5px;
}
.finish{
 	text-decoration: line-through;
 	color: #ccc;
}
.finishButton{
 	background-color: green;
 	color: #fff;
 	display: inline-block;
 	border:none;
 	margin-left: 10px;
 	border-radius: 5px;
 	padding: 2px;
}



